body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  font-family: Roboto, Helvetica, Arial, sans-serif; }

.title, .tower-title, .basestation-title, .remote-title {
  min-height: 40px;
  line-height: 40px;
  display: table;
  width: 100%;
  cursor: pointer; }

.tower-title {
  min-height: 60px;
  line-height: 60px; }

.container, .tower-container {
  color: #000000;
  font-family: Roboto, Helvetica, Arial, sans-serif;
  font-size: 13px;
  text-align: left;
  margin-bottom: 6px; }

.selected {
  width: calc(100% - 3px);
  background-color: #F6F6F6;
  border-left: 3px solid #267FFA; }

.tower-container {
  width: 288px;
  border: 1px solid #D4D4D4;
  min-height: 60px;
  position: relative; }

.deviceSearchContainer {
  width: 288px;
  margin-bottom: 30px; }

.status {
  margin-right: 12px;
  margin-left: 15px;
  display: inline-block; }
  .status .status-icon {
    margin-bottom: -3px;
    max-width: 20px; }

.basestation-container .status {
  margin-right: 14px;
  margin-left: 32px; }

.remote-container .status {
  margin-right: 14px;
  margin-left: 52px; }

.panel-body-expended {
  display: block; }

.panel-body-collapsed {
  display: none; }

.collapsible {
  display: inline-block;
  text-align: right; }

.not-collapsible {
  display: none; }

.noselect, .collapse-arrow {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }

.arrow-container {
  display: table-cell;
  vertical-align: middle;
  line-height: 16px;
  padding-bottom: 16px;
  background-color: inherit;
  cursor: pointer; }

.collapse-arrow {
  box-sizing: border-box;
  position: absolute;
  right: 24px;
  border: none; }

.arrow-up {
  -webkit-transform: rotate(270deg) scaleY(3);
          transform: rotate(270deg) scaleY(3); }

.arrow-down {
  -webkit-transform: rotate(90deg) scaleY(3);
          transform: rotate(90deg) scaleY(3); }

.line-6 {
  box-sizing: border-box;
  height: 1px;
  width: 67px;
  margin-left: 110.5px;
  background-color: #D4D4D4;
  margin-bottom: 6px; }
