.noselect, .collapse-arrow {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */ }

.arrow-container {
  display: table-cell;
  vertical-align: middle;
  line-height: 16px;
  padding-bottom: 16px;
  background-color: inherit;
  cursor: pointer; }

.collapse-arrow {
  box-sizing: border-box;
  position: absolute;
  right: 24px;
  border: none; }

.arrow-up {
  -webkit-transform: rotate(270deg) scaleY(3);
          transform: rotate(270deg) scaleY(3); }

.arrow-down {
  -webkit-transform: rotate(90deg) scaleY(3);
          transform: rotate(90deg) scaleY(3); }

.line-6 {
  box-sizing: border-box;
  height: 1px;
